import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { showErrMsg, showSuccessMsg } from '../utils/notification/Notification';
import { isLength, isMatch } from '../utils/validation/Validation';
import {
  fetchAllUsers,
  dispatchGetAllUsers,
} from '../../redux/actions/usersAction';

const initialState = {
  name: '',
  password: '',
  confirmPassword: '',
  err: '',
  success: '',
};

const Profile = () => {
  const auth = useSelector((state) => state.auth);
  const token = useSelector((state) => state.token);
  const users = useSelector((state) => state.users);

  const { user, isAdmin } = auth;
  const [data, setData] = useState(initialState);
  const { name, password, confirmPassword, err, success } = data;
  const [avatar, setAvatar] = useState(false);
  const [loading, setLoading] = useState(false);
  const [callback, setCallback] = useState(false); // mc ch l thy s thay i  re-render li page

  const dispatch = useDispatch();

  useEffect(() => {
    if (isAdmin) {
      fetchAllUsers(token).then((res) => {
        dispatch(dispatchGetAllUsers(res));
      });
    }
  }, [token, isAdmin, dispatch, callback]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({ ...data, [name]: value, err: '', success: '' });
  };

  const changeAvatar = async (e) => {
    e.preventDefault();
    try {
      const file = e.target.files[0];

      if (!file)
        return setData({ ...data, err: 'No files uploaded!', success: '' });

      if (file.size > 1024 * 1024) {
        return setData({ ...data, err: 'Size too large.', success: '' });
      }

      if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
        return setData({
          ...data,
          err: 'File format is incorrect.',
          success: '',
        });
      }

      let formData = new FormData();
      formData.append('file', file);

      setLoading(true);
      const res = await axios.post('/api/uploadAvatar', formData, {
        headers: {
          'content-type': 'multipart/form-data',
          Authorization: token,
        },
      });

      setLoading(false);
      setAvatar(res.data.url);
    } catch (error) {
      setData({ ...data, err: error.response.data.msg, success: '' });
    }
  };

  const updateInformation = async () => {
    try {
      await axios.patch(
        '/user/update',
        {
          name: name ? name : user.name,
          avatar: avatar ? avatar : user.avatar,
        },
        {
          headers: { Authorization: token },
        }
      );
      setData({ ...data, err: '', success: 'Updated Success !' });
    } catch (error) {
      setData({ ...data, err: error.response.data.msg, success: '' });
    }
  };

  const updatePassword = async () => {
    if (isLength(password)) {
      return setData({
        ...data,
        err: 'Password must be at least 6 characters.',
        success: '',
      });
    }

    if (!isMatch(password, confirmPassword)) {
      return setData({
        ...data,
        err: 'Password did not match.',
        success: '',
      });
    }
    try {
      await axios.post(
        '/user/resetPassword',
        { password },
        {
          headers: { Authorization: token },
        }
      );
      setData({ ...data, err: '', success: 'Updated Success !' });
    } catch (error) {
      setData({ ...data, err: error.response.data.msg, success: '' });
    }
  };

  const handleUpdate = () => {
    if (name || avatar) updateInformation();
    if (password) updatePassword();
  };

  const handleDelete = async (id) => {
    try {
      if (user._id !== id) {
        if (window.confirm('Are you sure you want to delete this account?')) {
          setLoading(true);
          await axios.delete(`/user/delete/${id}`, {
            headers: { Authorization: token },
          });
          setLoading(false);
          setCallback(!callback);
        }
      }
    } catch (error) {
      setData({ ...data, err: error.response.data.msg, success: '' });
    }
  };

  return (
    <>
      <div>
        {err && showErrMsg(err)}
        {success && showSuccessMsg(success)}
        {loading && <h3>Loading.....</h3>}
      </div>
      <div className='profile_page'>
        <div className='col-left'>
          <h2>{isAdmin ? 'Admin Profile' : 'User Profile'}</h2>
          <div className='avatar'>
            <img src={avatar ? avatar : user.avatar} alt='avatar' />
            <span>
              <i className='fa-solid fa-camera'></i>
              <p>Change</p>
              <input
                type='file'
                name='file'
                id='file_up'
                onChange={changeAvatar}
              />
            </span>
          </div>

          <div className='form-group'>
            <label htmlFor='name'>Name</label>
            <input
              type='text'
              name='name'
              id='name'
              placeholder='Your name'
              defaultValue={user.name}
              onChange={handleChange}
            />
          </div>

          <div className='form-group'>
            <label htmlFor='email'>Email</label>
            <input
              type='text'
              name='email'
              id='email'
              placeholder='Your email address'
              defaultValue={user.email}
              disabled
            />
          </div>

          <div className='form-group'>
            <label htmlFor='password'>New Password</label>
            <input
              type='password'
              name='password'
              id='password'
              placeholder='Your password'
              value={password}
              onChange={handleChange}
            />
          </div>

          <div className='form-group'>
            <label htmlFor='confirmPassword'>Confirm New Password</label>
            <input
              type='password'
              name='confirmPassword'
              id='confirmPassword'
              placeholder='Confirm password'
              value={confirmPassword}
              onChange={handleChange}
            />
          </div>

          <div>
            <em style={{ color: 'crimson' }}>
              * If you update your password here, you will not be able to login
              quickly using google and facebook
            </em>
          </div>

          <button disabled={loading} onClick={handleUpdate}>
            Update
          </button>
        </div>

        <div className='col-right'>
          <h2>{isAdmin ? 'Users' : 'MyOrders'}</h2>

          <div style={{ overflowX: 'auto' }}>
            <table className='customers'>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Admin</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {users.map((user) => (
                  <tr key={user._id}>
                    <td>{user._id}</td>
                    <td>{user.name}</td>
                    <td>{user.email}</td>
                    <td>
                      {user.role === 1 ? (
                        <i className='fa-solid fa-check' title='Admin'></i>
                      ) : (
                        <i className='fa-solid fa-xmark' title='User'></i>
                      )}
                    </td>
                    <td>
                      <Link to={`/editUser/${user._id}`}>
                        <i
                          className='fa-solid fa-pen-to-square'
                          title='Edit'
                        ></i>
                      </Link>
                      <i
                        className='fa-solid fa-trash'
                        title='Remove'
                        onClick={() => handleDelete(user._id)}
                      ></i>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;
